.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem; 
    position: fixed;
    width: 100%;
    top: 0;
    left: 0; 
    z-index: 100;

    .gpt3__navbar-links {
        flex: 1;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        .gpt3__navbar-links_logo {
            margin-right: 2rem;

            img{
                width: 62.56px;
                height: 16.02px;
            }
        }

        .gpt3__navbar-links_container{
            display: flex;
            flex-direction: row;

            p {
                color: #fff;
                font-family: var(--font-family);
                font-weight: 500;
                font-size: 18px;
                line-height: 25px;
                text-transform: capitalize;
                margin: 0 1rem;
                cursor: pointer;
            }
        }
    }
    .gpt3__navbar-sign {
        display: flex;
        justify-content: flex-end;
        align-items: center;

        p {
            color: #fff;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 18px;
            line-height: 25px;
            text-transform: capitalize;
            margin: 0 1rem;
            cursor: pointer;
        }

        button {
            padding: 0.5rem 1rem;
            color: #fff;
            background: #ff4820;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 18px;
            line-height: 25px;
            border-radius: 5px;
            border: 0;
            outline: none;
            cursor: pointer;
        }
    }
    .gpt3__navbar-menu_container {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;
        text-align: end;
        background: var(--color-footer);
        padding: 2rem;
        position: absolute;
        top: 40px;
        right: 0;
        margin-top: 1rem;
        min-width: 210px;
        border-radius: 5px;
        box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);


        p {
            color: #fff;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 18px;
            line-height: 25px;
            text-transform: capitalize;
            margin: 1rem 0;
            cursor: pointer;
        }

        .gpt3__navbar-menu_container-links-sign {
            display: none;
        }
    }

    .gpt3__navbar-menu {
        margin-left: 1rem;
        display: none;
        position: relative;

        svg{
            cursor: pointer;
        }
    }
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar{
        .gpt3__navbar-links {
            .gpt3__navbar-links_container {
                display: none;
            }
        }
        .gpt3__navbar-menu {
            display: flex;
        }
        
    }
    
}

@media screen and (max-width: 700px) {
    .gpt3__navbar{
        padding: 2rem 4rem; 
        
    }
    
}

@media screen and (max-width: 550px) {
    .gpt3__navbar{
        padding: 2rem;

        .gpt3__navbar-menu_container {
            top: 20px;

            .gpt3__navbar-menu_container-links{

                .gpt3__navbar-menu_container-links-sign{
                    display: block;

                    button {
                        padding: 0.5rem 1rem;
                        color: #fff;
                        background: #ff4820;
                        font-family: var(--font-family);
                        font-weight: 500;
                        font-size: 18px;
                        line-height: 25px;
                        border-radius: 5px;
                        border: 0;
                        outline: none;
                        cursor: pointer;
                    }
                }
            }
        }

        .gpt3__navbar-sign {
            display: none;
        } 
    }
    
}