.gpt3__header {
    display: flex; 
    padding-top: 5.5rem ;

    .gpt3__header-content{
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        margin-right: 5rem;

        h1 {
            font-family: var(--font-family);
            font-weight: 800;
            font-size: 62px;
            line-height: 75px;
            letter-spacing: -0.04em;
        }

        p{
            font-family: var(--font-family);
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 27px;
            color: var(--color-text);
            margin-top: 1.5rem;
        }

        .gpt3__header-content__input{
            width: 100%;
            margin: 2rem 0 1rem;
            display: flex;
            flex-direction: row;

            input{
                flex: 2;
                width: 100%;
                min-height: 50px;
                font-family: var(--font-family);
                font-style: normal;
                font-weight: normal;
                font-size: 20px;
                line-height: 27px;
                background: var(--color-footer);
                border: 2px solid var(--color-footer);
                padding: 0 1rem;
                outline: none;
                color: #fff;
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;
            }

            button{
                flex: 0.6;
                width: 100%;
                min-height: 50px;
                font-family: var(--font-family);
                font-weight: bold;
                border-top-right-radius: 5px;
                border-bottom-right-radius: 5px;

                background: #FF4820; 
                font-style: normal;
                font-size: 20px;
                line-height: 27px;
                color: #fff;
                cursor: pointer;
                outline: none;
                padding: 0 1rem;
                border: none;
            }
        }
        .gpt3__header-content_peaple {
            width: 100%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-top: 2rem;

            img{
                width: 181.79px;
                height: 38px;
            }

            p{
                padding-bottom: 1.5rem;
                margin-left: 1rem;
                font-family: var(--font-family);
                font-style: normal;
                font-weight: 500;
                font-size: 12px;
                line-height: 38px; 
                color: #FFFFFF;
                text-align: center;
            }

        }
    }
    .gpt3__header-image {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;

        img{
            width: 100%;
            height: 100%;
        }
    }
}

@media screen and (max-width:1050px) {
    .gpt3__header{
        flex-direction: column;

        .gpt3__header-content{
            margin: 0 0 3rem;
        }
    }
}

@media screen and (max-width:650px) {
    .gpt3__header{

        .gpt3__header-content{
            h1{
                font-size: 48px;
                line-height: 60px;
            }
            p{
                font-size: 16px;
                line-height: 24px;
            }
            .gpt3__header-content_peaple {
                flex-direction: column;

                p{
                    margin: 0;
                }
            }

            .gpt3__header-content__input{

                input, button{
                    font-size: 16px;
                    line-height: 24px;
                }
            }
        }
    }
}

@media screen and (max-width:490px) {
    .gpt3__header{

        .gpt3__header-content{
            h1{
                font-size: 36px;
                line-height: 48px;
            }
            p{
                font-size: 14px;
                line-height: 24px;
            }

            .gpt3__header-content__input{

                input, button{
                    font-size: 12px;
                    line-height: 16px;
                }
            }
        }
    }
}