.gpt3__possibility {
    display: flex;
    
    .gpt3__possibility-image {
        flex: 1;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    
        margin-right: 2rem;

        img {
            width: 100%;
            height: 100%;
        }
    }

    .gpt3__possibility-content {
        flex: 1;
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
        flex-direction: column;

        h4 {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 16px;
            line-height: 30px;
            color: #71E5FF;
        }

        h4:last-child {
            color: #ff8A71;
        }

        h1 {
            font-family: var(--font-family);
            font-weight: 800;
            font-size: 34px;
            line-height: 45px;
            margin: 1rem 0;
        }

        p {
            color: var(--color-text);
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 30px;
        
            margin-bottom: 2rem;
        }
    }
}    
@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;

        .gpt3__possibility-image {
            margin: 1rem 0;

            img {
                width: 100%;
                height: 100%;
            }
        }

        .gpt3__possibility-content {
            margin-top: 2rem;
        }
    } 

    
}

@media screen and (max-width: 700px) {
    .gpt3__possibility{
        .gpt3__possibility-image {
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
    
}
